import HelloWorld from "@/components/HelloWorld.vue";

export default {
    name: "page-home",
    data: () => {
        return {
            a: Math.random(),
            i: 0
        };
    },
    created() {
        console.warn("home ")

    },
    methods: {
        aa() {
            $(".page-scroller-transition").css({});
        },
        onReachTop() {
            ++this.i;
            console.log("home ");
        },
        onTriggerTop(fn_loadFinished) {
            console.log("home  TriggerTop ");
            setTimeout(() => {
                console.log("home  TriggerTop load finished");
                fn_loadFinished();
            }, 1000)
        }
    },
    components: {
        HelloWorld
    }
};